import React, { FC, ReactElement } from "react";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import ExportToExcel from "../../components/GenerateReport/ExportToExcel";
import { SchoolDataNew } from "./PlacementListDDE";

type NonReportedSchoolPlacementReportProps = {
  close: () => void;
  schools: SchoolDataNew[];
  title: string;
};

const NonReportedSchoolPlacementReport: FC<
  NonReportedSchoolPlacementReportProps
> = ({ close, schools, title }): ReactElement => {
  return (
    <DashboardContainer>
      <div className="flex w-full pb-4 pt-2 items-center">
        <div className="flex-1">
          <h2 className="text-2xl font-bold">{title}</h2>
        </div>
        <div className="text-right">
          {schools.length >= 1 && (
            <ExportToExcel
              fileData={schools}
              fileName={`${title} - ${new Date().toISOString()}`}
              btnName="Download EXCEL"
            />
          )}
        </div>
      </div>
      <table className="w-full">
        <thead>
          <tr className="text-left">
            <th className="border p-1 ">#</th>
            <th className="border p-1 ">School code</th>
            <th className="border p-1 ">School name</th>
            <th className="border p-1 ">Region code</th>
            <th className="border p-1 ">School status</th>
            <th className="border p-1 ">Has report</th>
          </tr>
        </thead>
        <tbody>
          {schools.map((el, i) => (
            <tr>
              <td className="border p-1 ">{i + 1}</td>
              <td className="border p-1 ">{el.school_code}</td>
              <td className="border p-1 ">{el.school_name}</td>
              <td className="border p-1 ">{el.region_code}</td>
              <td className="border p-1 ">{el.school_status}</td>
              <td className="border p-1 text-center">
                {el.has_report === true ? (
                  <span className="p-0.5 text-xs rounded-full bg-green-100 text-green-600 px-3">
                    Report found
                  </span>
                ) : (
                  <span className="p-0.5 text-xs rounded-full bg-red-100 text-red-600 px-3">
                    No Report
                  </span>
                )}
              </td>
            </tr>
          ))}
          {schools.length <= 0 && (
            <tr>
              <td colSpan={10} className="py-8 text-center">
                <h2 className="text-3xl text-gray-500">No results found!</h2>
              </td>
            </tr>
          )}
        </tbody>
      </table>
    </DashboardContainer>
  );
};
export default NonReportedSchoolPlacementReport;
