import React, { Fragment } from "react";
import { MdCheckBox, MdCheckBoxOutlineBlank } from "react-icons/md";

import { connect } from "react-redux";
import {
  Auth,
  Basic,
  FC_GetPlacementByDistrict,
  API_GetPlacementBySchool,
  Placement,
  FT_GetPlacementByDistrict,
  FC_ApprovePlacementList,
  FT_ApprovePlacementList,
  FC_RejectPlacementList,
  FC_GetSchoolsByDistrict,
  SchoolData,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import InputSelect from "../../components/Inputs/InputSelect";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import PlacementListPreview from "../../components/PlacementListPreview/PlacementListPreview";
import { StoreState } from "../../reducers";
import { checkAccess, RenderPageAccess, UsersAccess } from "../../utils/access";
import { DATE, MONTHS_LIST, search } from "../../utils/functions";
import NonReportedSchoolPlacementReport from "./NonReportedSchoolPlacementReport";
// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
  placement: Placement;
  FC_GetPlacementByDistrict: FT_GetPlacementByDistrict;
  FC_ApprovePlacementList: FT_ApprovePlacementList;
  FC_RejectPlacementList: FT_ApprovePlacementList;
}

export interface SchoolDataNew extends SchoolData {
  has_report: boolean;
}

type StateTab = "placement" | "non-reported-schools" | "reported-schools";
interface AppState {
  loading: boolean;
  error: {
    target:
      | "main-success"
      | "main-error"
      | "placement-error"
      | "placement-success"
      | "loading-schools-success"
      | "loading-schools-error"
      | null;
    msg: string;
  };
  data: API_GetPlacementBySchool[];
  selected: API_GetPlacementBySchool | null;
  search: string;
  selected_month: string;
  selected_status: string;
  dde_comment: string;
  approve_loading: boolean;
  accepted_changes: boolean;
  all_schools: SchoolDataNew[];
  loading_schools: boolean;
  tab: StateTab;
}

class _PlacementListDDE extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      data: [],
      error: {
        target: null,
        msg: "",
      },
      selected: null,
      search: "",
      selected_month: MONTHS_LIST[new Date().getMonth()],
      selected_status: "",
      dde_comment: "",
      approve_loading: false,
      accepted_changes: false,
      all_schools: [],
      loading_schools: true,
      tab: "placement",
    };
  }

  selectItem = (data: API_GetPlacementBySchool | null) =>
    this.setState({
      selected: data,
      dde_comment:
        data === null
          ? ""
          : data.dde_comment === null || data.dde_comment === ""
          ? `I,  ${this.props.auth.user?.first_name} ${
              this.props.auth.user?.middle_name
            } ${this.props.auth.user?.last_name} with Staff code: ${
              this.props.auth.user?.staff_code
            } and with NID: ${this.props.auth.user?.nid}, DDE of ${
              this.props.auth.district?.district_name
            } district confirm that the list of teachers displayed on the teacher placement report from ${
              data?.school_name
            } school for ${MONTHS_LIST[new Date().getMonth()]}, on ${DATE(
              new Date()
            )} is valid. I also confirm that every teacher is assigned to his/her teaching qualification as per their appointment letters.`
          : data.dde_comment,
      error: {
        target: null,
        msg: "",
      },
    });

  submitLogin = (e: any) => {
    e.preventDefault();
  };

  componentDidMount = () => {
    RenderPageAccess(
      this.props.auth.role?.access,
      UsersAccess.DDE_VIEW_PLACEMENT_LIST,
      () => {
        this.startApp();
      }
    );
  };

  startApp = () => {
    if (this.props.basic.academic_calender === null) {
      this.setState({
        error: {
          target: "main-error",
          msg: "failed to load data, please reload the page (Refresh)",
        },
      });
    } else if (this.props.auth.district === null) {
      this.setState({
        error: {
          target: "main-error",
          msg: "No school assigned to you",
        },
      });
    } else {
      let district_code = this.props.auth.district.district_code;
      this.setState({
        error: {
          target: null,
          msg: "",
        },
        selected_month: MONTHS_LIST[new Date().getMonth()],
      });
      this.props.FC_GetPlacementByDistrict(
        {
          district_code,
          academic_id: this.props.basic.academic_calender.academic_year_id,
        },
        (status, msg) => {
          if (status === true) {
            console.log({ status, msg });
            this.setState({
              loading: false,
            });

            this.loadAllSchools(district_code);
          } else {
            this.setState({
              loading: false,
              error: {
                msg: msg,
                target: "main-error",
              },
            });
          }
        }
      );
    }
  };

  changeTab = (tab: StateTab) =>
    this.setState({
      tab: tab,
    });
  // PlacementListDDE

  loadAllSchools = (district_id: string) => {
    this.setState({
      loading_schools: true,
    });
    FC_GetSchoolsByDistrict(district_id, (status, data, msg) => {
      if (status === true && data !== null) {
        this.setState({
          loading_schools: false,
          all_schools: data.map((sch) => {
            // Check if the schools has reported
            let findReport = this.props.placement.placements.find(
              (el) => el.school_code + "" === sch.school_code + ""
            );

            return {
              ...sch,
              has_report: findReport === undefined ? false : true,
            };
          }),
        });
      } else {
        this.setState({
          loading_schools: false,
          error: {
            target: "loading-schools-error",
            msg: `Error: ${msg}`,
          },
        });
      }
    });
  };

  approvePlacementList(placement_id: string) {
    if (this.state.selected === null) return;
    if (window.confirm("Are you sure you want to approve this list")) {
      this.setState({
        approve_loading: true,
      });
      // let placement_id = ;
      let dde_comment = this.state.dde_comment;
      this.props.FC_ApprovePlacementList(
        {
          dde_comment: dde_comment,
          placement_id: placement_id,
        },
        (status, msg) => {
          if (status === true) {
            // console.log({
            //   dde_comment,
            //   placement_id,
            //   data: this.state.data,
            // });

            this.setState({
              approve_loading: false,
              error: {
                target: "placement-success",
                msg: msg,
              },

              selected:
                this.state.selected === null
                  ? null
                  : {
                      ...this.state.selected,
                      dde_approval_status: "1",
                      dde_comment: this.state.dde_comment,
                    },
            });
          } else {
            this.setState({
              approve_loading: false,
              error: {
                target: "placement-error",
                msg: msg,
              },
            });
          }
        }
      );
    }
  }

  rejectPlacementList(placement_id: string) {
    if (this.state.selected === null) return;
    if (window.confirm("Are you sure you want to reject this list")) {
      this.setState({
        approve_loading: true,
      });
      // let placement_id = ;
      let dde_comment = this.state.dde_comment;
      this.props.FC_RejectPlacementList(
        {
          dde_comment: dde_comment,
          placement_id: placement_id,
        },
        (status, msg) => {
          if (status === true) {
            console.log({
              dde_comment,
              placement_id,
              data: this.state.data,
            });

            this.setState({
              approve_loading: false,
              error: {
                target: "placement-success",
                msg: msg,
              },

              selected:
                this.state.selected === null
                  ? null
                  : {
                      ...this.state.selected,
                      dde_approval_status: "0",
                      dde_comment: this.state.dde_comment,
                    },
            });
          } else {
            this.setState({
              approve_loading: false,
              error: {
                target: "placement-error",
                msg: msg,
              },
            });
          }
        }
      );
    }
  }

  calculatePlacementList = (month: string) => {
    // setTimeout(() => {
    this.setState((e) => ({
      selected_month: month,
      // all_schools:
    }));
    // }, 10);
  };

  render() {
    let SchoolsList = this.state.all_schools.map((sch) => {
      // Check if the schools has reported
      let findReport = this.props.placement.placements.find(
        (el) =>
          el.school_code + "" === sch.school_code + "" &&
          el.month.toLowerCase() === this.state.selected_month.toLowerCase()
      );

      return {
        ...sch,
        has_report: findReport === undefined ? false : true,
      };
    });

    let TotalReportedSchools = SchoolsList.filter(
      (el) => el.has_report === true
    );
    let TotalNonReportedSchools = SchoolsList.filter(
      (el) => el.has_report === false
    );

    let RequestsList = this.props.placement.placements;
    //* Filter by months
    RequestsList =
      this.state.selected_month === ""
        ? RequestsList
        : RequestsList.filter(
            (itm) =>
              itm.month.toLowerCase() ===
              this.state.selected_month.toLowerCase()
          );

    //* Status
    RequestsList =
      this.state.selected_status === ""
        ? RequestsList
        : RequestsList.filter(
            (itm) => itm.dde_approval_status === this.state.selected_status
          );

    //* Filter by school name
    RequestsList = search(RequestsList, this.state.search, {
      school_code: true,
      school_name: true,
      dde_name: true,
      ht_comment: true,
      month: true,
      district_name: true,
      head_teacher_name: true,
    });

    if (this.state.loading === true)
      return (
        <div className="p-8 text-center">
          <div className="mx-auto">
            <Loading />
          </div>
        </div>
      );

    if (this.state.error.target === "main-error") {
      return (
        <Alert theme="danger" fixed>
          {this.state.error.msg}
        </Alert>
      );
    }

    if (this.state.selected !== null) {
      let data_json = JSON.parse(this.state.selected.list);
      return (
        <>
          <PlacementListPreview
            placement_list={data_json ? data_json : []}
            header={
              <>
                <div className="flex">
                  <div className="text-xl flex-1">
                    <h2>
                      School code: <b>{this.state.selected.school_code}</b>
                    </h2>
                    <h2>
                      School name: <b>{this.state.selected.school_name}</b>
                    </h2>
                    <h2>
                      Done by: <b>{this.state.selected.head_teacher_name}</b>
                    </h2>
                  </div>
                  <div className="flex-1 text-right">
                    <Button
                      theme="danger-light"
                      onClick={this.selectItem.bind(this, null)}
                    >
                      Close
                    </Button>
                  </div>
                </div>

                <h1 className="text-center text-3xl font-bold mb-4 my-3">
                  Placement list for <b>{this.state.selected.month}</b>
                </h1>
              </>
            }
            footer={
              <>
                <div className="py-3">
                  <br />
                  <br />
                  <h5>
                    <b>Headteacher comment: </b>
                    {this.state.selected.ht_comment}
                  </h5>
                  <h5>
                    <b>DDE comment: </b>
                    {this.state.selected.dde_comment}
                  </h5>
                  <br />
                  <br />
                </div>

                {this.state.error.target === "main-success" && (
                  <Alert theme="success" fixed>
                    {this.state.error.msg}
                  </Alert>
                )}

                {this.state.error.target === "placement-error" && (
                  <Alert theme="danger">{this.state.error.msg}</Alert>
                )}
                {this.state.error.target === "placement-success" && (
                  <Alert theme="success">{this.state.error.msg}</Alert>
                )}

                <div className="pt-5 text-right">
                  {checkAccess(
                    this.props.auth.role?.role,
                    UsersAccess.DDE_APPROVE_PLACEMENT_LIST
                  ) === false && (
                    <>
                      {this.state.selected.dde_approval_status === "0" && (
                        <div className="flex border-t mt-2 pt-2">
                          <div className="flex-1"></div>
                          <div className="flex-1 mt-2">
                            <span className="block text-left">Comment:</span>
                            <div
                              // disabled={this.state.approve_loading}
                              // onChange={(e) =>
                              //   this.setState({
                              //     dde_comment: e.target.value,
                              //   })
                              // }
                              className="w-full  border-2 bg-blue-200 rounded p-2 text-left"
                            >
                              {this.state.dde_comment}
                            </div>

                            <div
                              onClick={() =>
                                this.setState({
                                  accepted_changes:
                                    !this.state.accepted_changes,
                                })
                              }
                              className={`flex my-2 p-1 bg-gray-100 rounded gap-2 cursor-pointer ${
                                this.state.accepted_changes === true
                                  ? "text-green-600"
                                  : "text-gray-800"
                              }`}
                            >
                              <div className="">
                                {this.state.accepted_changes === true ? (
                                  <MdCheckBox className="text-3xl" />
                                ) : (
                                  <MdCheckBoxOutlineBlank className="text-3xl" />
                                )}
                              </div>
                              <div className="flex-1 ">
                                <p className="text-xl text-left">
                                  I have read and agree/agreed with the comment.
                                </p>
                              </div>
                            </div>

                            {this.state.accepted_changes === true && (
                              <Button
                                size="lg"
                                theme="success"
                                className="ml-2"
                                loading={this.state.approve_loading}
                                onClick={this.approvePlacementList.bind(
                                  this,
                                  this.state.selected.placement_list_approval_id
                                )}
                              >
                                Approve list
                              </Button>
                            )}
                          </div>
                        </div>
                      )}
                      <Button
                        // size="lg"
                        theme="danger-light"
                        className="ml-2"
                        loading={this.state.approve_loading}
                        onClick={this.rejectPlacementList.bind(
                          this,
                          this.state.selected.placement_list_approval_id
                        )}
                      >
                        Reject placement list
                      </Button>
                    </>
                  )}
                </div>
              </>
            }
          />
        </>
      );
    }

    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.loading_schools === true && (
          <DashboardContainer transparent>
            <Alert theme="info">
              <span className="animate__animated animate__fadeIn animate__infinite ">
                Searching non submitted schools...
              </span>
            </Alert>
          </DashboardContainer>
        )}

        {this.state.loading_schools === false &&
          this.state.all_schools.length >= 1 && (
            <DashboardContainer transparent>
              <div className="w-full flex items-center">
                <div className=" flex gap-2">
                  <section className="p-1 bg-white rounded shadow px-4">
                    <span className="text-sm text-gray-600">Total schools</span>
                    <h1 className="font-bold text-2xl -mt-1">
                      {this.state.all_schools.length}
                    </h1>
                  </section>

                  <section className="p-1 bg-green-100 text-green-600 rounded shadow px-4">
                    <span className="text-sm opacity-80">Reported schools</span>
                    <h1 className="font-bold text-2xl -mt-1">
                      {TotalReportedSchools.length}
                    </h1>
                  </section>

                  <section className="p-1 bg-red-50 text-red-600 rounded shadow px-4">
                    <span className="text-sm  opacity-80">Non Reported</span>
                    <h1 className="font-bold text-2xl -mt-1">
                      {TotalNonReportedSchools.length}
                    </h1>
                  </section>
                </div>
                <div className="flex-1 text-right">
                  <div className="p-1 inline-block bg-white rounded-lg">
                    <button
                      onClick={this.changeTab.bind(this, "placement")}
                      className={`p-1 rounded-lg hover:bg-yellow-100 ${
                        this.state.tab === "placement"
                          ? "bg-yellow-500 bg-opacity-30 text-yellow-700 font-bold"
                          : ""
                      }  px-4 py-2`}
                    >
                      Placement reports
                    </button>
                    <button
                      onClick={this.changeTab.bind(this, "reported-schools")}
                      className={`p-1 rounded-lg hover:bg-yellow-100 ${
                        this.state.tab === "reported-schools"
                          ? "bg-yellow-500 bg-opacity-30 text-yellow-700 font-bold"
                          : ""
                      }  px-4 py-2`}
                    >
                      Reported schools
                    </button>
                    <button
                      onClick={this.changeTab.bind(
                        this,
                        "non-reported-schools"
                      )}
                      className={`p-1 rounded-lg hover:bg-yellow-100 ${
                        this.state.tab === "non-reported-schools"
                          ? "bg-yellow-500 bg-opacity-30 text-yellow-700 font-bold"
                          : ""
                      }  px-4 py-2`}
                    >
                      Non Reported schools
                    </button>
                  </div>
                </div>
              </div>
            </DashboardContainer>
          )}

        {/* //* Placement reports */}
        {this.state.tab === "placement" && (
          <DashboardContainer>
            <div className="flex items-center">
              <div className="flex-1">
                <Heading>
                  Placement List for {this.props.auth.district?.district_name}
                </Heading>
                <p className="opacity-60 text-lg -mt-1 font-bold text-accent-900 dark:text-accent-800">
                  <b>
                    {this.props.basic.academic_calender?.academic_year_name}{" "}
                  </b>
                </p>
              </div>
              <div className="flex pl-4">
                <InputSelect
                  data={[
                    // { title: "All", value: "" },
                    ...MONTHS_LIST.map((itm) => ({ title: itm, value: itm })),
                  ]}
                  onChange={(e) => {
                    // this.setState({ selected_month: e.target.value });
                    this.calculatePlacementList(e.target.value);
                  }}
                  value={this.state.selected_month}
                  error={""}
                  label={"Month"}
                  placeholder={"Select a month"}
                  name={"month"}
                  size="sm"
                />
              </div>
              <div className="flex pl-8">
                <div className="">
                  <p className="m-0">Total</p>
                  <h2 className="text-3xl font-bold -mt-1">
                    {RequestsList.length}
                  </h2>
                </div>
              </div>
            </div>

            <div className="mt-3">
              <div className="mb-2 border-opacity-30 border rounded flex px-3 items-center">
                <div className="flex-1 mx-3">
                  <TextInput
                    onChange={(e) => this.setState({ search: e.target.value })}
                    value={this.state.search}
                    error={""}
                    label={"Search"}
                    placeholder={"School name"}
                    name={"search"}
                    size="sm"
                  />
                </div>

                <div className="">
                  {/* selected_status */}
                  <InputSelect
                    data={[
                      { title: "All", value: "" },
                      { title: "Approved", value: "1" },
                      { title: "Waiting", value: "0" },
                    ]}
                    onChange={(e) =>
                      this.setState({ selected_status: e.target.value })
                    }
                    value={this.state.selected_status}
                    error={""}
                    label={"Status"}
                    placeholder={"Select status"}
                    name={"month"}
                    size="sm"
                  />
                </div>
              </div>
              <table className="w-full text-left table-auto border">
                <thead>
                  <tr>
                    <th></th>
                    <th>School Code</th>
                    <th>School Name</th>
                    <th>Month</th>
                    <th>Created by</th>
                    <th>Approved by</th>
                    <th>Approved date</th>
                    <th>Status</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {RequestsList.length <= 0 ? (
                    <tr>
                      <td
                        colSpan={8}
                        className="p-8 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                      >
                        <Heading size="md" className="opacity-60 font-light">
                          No results found
                        </Heading>
                      </td>
                    </tr>
                  ) : (
                    RequestsList.map((req, i) => {
                      return (
                        <Fragment key={i}>
                          <tr
                            className={`group border rounded hover:bg-blue-500 hover:bg-opacity-20  `}
                          >
                            <td
                              className="p-1 border cursor-pointer text-center"
                              key={i}
                            >
                              {i + 1}
                            </td>
                            <td className="p-2 border">{req.school_code}</td>
                            <td className="p-2 border">{req.school_name}</td>
                            <td className="p-2 border">{req.month}</td>
                            <td className="p-2 border">
                              {req.head_teacher_name}
                            </td>
                            <td className="p-2 border">{req.dde_name}</td>
                            <td className="p-2 border">{req?.approved_date}</td>
                            <td className="p-2 border">
                              {req.dde_approval_status === "1" ? (
                                <span className="p-0.5 text-sm rounded-full px-2 bg-green-200 font-bold text-green-700">
                                  Approved
                                </span>
                              ) : (
                                <span className="p-0.5 font-bold text-sm rounded-full px-2 bg-gray-200 text-gray-700">
                                  Waiting
                                </span>
                              )}
                            </td>

                            <td className="p-2 w-28 text-right">
                              <Button
                                size="sm"
                                className="w-full"
                                onClick={this.selectItem.bind(this, req)}
                                theme="accent-light"
                              >
                                View list
                              </Button>
                            </td>
                          </tr>
                        </Fragment>
                      );
                    })
                  )}
                </tbody>
              </table>
            </div>
          </DashboardContainer>
        )}

        {/* //* Placement reports */}

        {this.state.tab === "non-reported-schools" && (
          <NonReportedSchoolPlacementReport
            schools={TotalNonReportedSchools}
            close={this.changeTab.bind(this, "placement")}
            title="Non Reported schools"
          />
        )}

        {this.state.tab === "reported-schools" && (
          <NonReportedSchoolPlacementReport
            schools={TotalReportedSchools}
            close={this.changeTab.bind(this, "placement")}
            title="Reported schools"
          />
        )}
        <div className="h-24"></div>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  placement,
}: StoreState): { auth: Auth; basic: Basic; placement: Placement } => {
  return { auth: auth, basic: basic, placement: placement };
};

export const PlacementListDDE = connect(mapStateToProps, {
  FC_GetPlacementByDistrict,
  FC_ApprovePlacementList,
  FC_RejectPlacementList,
})(_PlacementListDDE);
